@import "../../variables.scss";
@import "../../index.scss";

.signup-container {
  @extend %container;
  .arrow-btn {
    cursor: pointer;
    position: absolute;
    top: 47%; left: 28vw;
    width: 50px;
    opacity: 0.1;
    &:hover {
      opacity: 1;
    }
    &:active {
      opacity: 0.3;
    }
  }
}

.inactive-block {
  &:before {
    content: '';
    background-color: $light-color;
    margin-left: 12vw;
    position: absolute;
    top: 50%;
    width: 25vw; height: 1px;
  }
  &:last-child {
    &:before {
      content: '';
      background-color: $light-color;
      margin-left: 2vw;
      position: absolute;
      top: 50%;
      width: 10vw; height: 1px;
    }
  }
}

.active-block {
  @extend %active-container;
}

.submit-block {
  @extend %user-show;
  align-items: center;
  div {
    display: flex;
    margin: 40px;
    button {
      margin: 0 10px;
      &:first-child {
        color: $light-color;
        @extend %bordered-btn;
      }
      &:last-child {
        @extend %accent-btn;
      }
    }
  }
}

.error-block {
  .error {
    @extend .error;
    margin: 0 0 40px;
    text-transform: none;
    font-size: 18px;
  }
  button {
    @extend %accent-btn;
  }
}

.done-block {
  &:before {
    content: '';
    background-color: $light-color;
    margin-left: -13.5vw;
    position: absolute;
    top: 50%;
    width: 18vw; height: 1px;
  }
  &:first-child {
    &:before {
      content: '';
      background-color: $light-color;
      margin-left: -6vw;
      position: absolute;
      top: 50%;
      width: 6vw; height: 1px;
    }
  }
  &:nth-child(3) {
    &:before {
      content: '';
      background-color: $light-color;
      margin-left: -13.5vw;
      position: absolute;
      top: 50%;
      width: 15vw; height: 1px;
    }
  }
}
